<template>
  <div>
    <a-card id="newdevice" title='添加设备'>
      <span class="back" slot="extra" @click="back">返回上一页</span>
      <div class="">
        <a-input class="input-line"
          placeholder="设备ID" 
          size='large' 
          v-model="deviceId"
          @change="change"
        >
          <span slot="addonBefore">注册设备</span>
        </a-input>
        <a-input class="input-line"
          placeholder="type" 
          size='large'
          v-model="deviceType"
          @change="change"
        >
          <span slot="addonBefore">设备类型</span>
        </a-input>
        <a-button style="margin-left: 20px;" 
          type='primary'
          @click="register"
        >
          确定
        </a-button>
        <a-button style="margin-left: 20px;"
          @click="reset"
        >
          重置
        </a-button>
      </div>
    </a-card>
  </div>
</template>

<script>
import {Card, Input, Button} from 'ant-design-vue'

export default {
  components: {
    ACard: Card,
    AInput: Input,
    AButton: Button
  },
  data() {
    return {
      deviceId: null,
      deviceType: null
    }
  },
  methods: {
    change() {
      // console.log(`device id: ${this.deviceId}, device type: ${this.deviceType}`)
    },
    register() {
      // axios
      this.postRegister()
      this.getDevices()
      this.getRefresh()
      this.$router.push({name: 'setting'})
    },
    reset() {
      console.log('reset')
      this.deviceId = null
      this.deviceType = null
    },
    postRegister() {
      const ip = this.$store.state.serverIP
      const registerRouter = ip + '/cam/register'
      this.axios.post(registerRouter, this.deviceForm)
        .then(() => {})
    },
    getRefresh() {
      const ip = this.$store.state.serverIP
      const getRouter = ip + '/cam/refresh'
      this.axios.get(getRouter)
        .then(response => {
          console.log(response)
          const data = response.data
          if(data.code === 200) {
            this.$store.commit('refresh', data.data)
          }
        })
    },
    getReceiver() {
      const ip = this.$store.state.serverIP
      const getRouter = ip + '/cam/receiver'
      this.axios.get(getRouter)
        .then(response => {
          console.log(response)
          const data = response.data
          if(data.code === 200) {
            this.$store.commit('refresh', data.data)
          }
        })
    },
    getDevices() {
      const ip = this.$store.state.serverIP
      const getRouter = ip + '/cam/devices'
      this.axios.get(getRouter)
        .then(response => {
          console.log(response)
          const data = response.data
          if(data.code === 200) {
            this.$store.commit('updateDevices', data.data)
          }
        })
    },
    back() {
      this.$router.go(-1)
    }
  },
  computed: {
    deviceForm() {
      return {
        deviceId: this.deviceId,
        deviceType: this.deviceType
      }
    },
    usernameParam() {
      return '?username=' + this.$store.state.username
    }
  }
}
</script>

<style>
#newdevice {
  width: 40%;
}

.input-line {
  margin: 20px 0;
}

.back {
  color: rgb(0, 153, 255);
  cursor: pointer;
}
</style>